import uniIn from "../../../components/uiOneInput.vue"
export default {
	data() {
		return {
			Show: false,
			count: "60",
			timer: "",
			mobile: "",
			num: '',
			numLength: 0,
			code: "",
			type:'', //login 修改密码
			disabled: false
		};
	},
	components: {
		uniIn
	},
	methods: {
		getNum(e) {
			this.num = e
			this.numLength = e.length
		},
		//重新获取验证码
		getcode() {
			this.$store.dispatch('signupCode',{
				mobile:this.mobile,
				type: 1
			}).then(res=>{
				this.Show = true
				const TIME_COUNT = 60;
				if (!this.timer) {
					this.count = TIME_COUNT;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
						} else {
							this.Show = false;
							this.count = 60;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000);
				}
			}).catch(err=>{})
		},
		submit() {
			this.disable = true
			this.$store.dispatch('getCheckCode',{
				code: this.num
			}).then(res=>{
				this.disabled = false
				let url = this.type=='login'?'/pages/mine/setting/new-password':'/pages/mine/setting/new-payword'
				uni.navigateTo({
					url: url
				})
			}).catch(err=>{
				this.disabled = false
			})
		}
	},
	onLoad(e){
		this.type = e.from
	},
	onShow() {
		this.$store.dispatch('getMobile',{}).then(res=>{
			this.mobile = res.data.mobile
			this.getcode()
		}).catch(err=>{})
	}
};
